import React from 'react';
import "../styles/Navbar.css";

// Here we are using object destructuring assignment to pluck off our variables from the props object
// We assign them to their own variable names
function Header({ currentPage, handlePageChange }) {
  return (
    <header className="fixed-top">
        <div className="container-fluid">
            <a className="navbar-brand" 
            href="#home"
            onClick={() => handlePageChange('Home')}
            >Zahra Mertens Portfolio
            </a>
        </div>
        <ul className="nav nav-tabs justify-content-end">  
            <li className="nav-item">
                <a
                href="#home"
                onClick={() => handlePageChange('Home')}
                // This is a conditional (ternary) operator that checks to see if the current page is "Home"
                // If it is, we set the current page to 'nav-link-active', otherwise we set it to 'nav-link'
                className={currentPage === 'Home' ? 'nav-link active active-link' : 'nav-link'}
                >
                Home
                </a>
            </li>
            <li className="nav-item">
                <a
                href="#about"
                onClick={() => handlePageChange('About')}
                // Check to see if the currentPage is `About`, and if so we use the active link class from bootstrap. Otherwise, we set it to a normal nav-link
                className={currentPage === 'About' ? 'nav-link active active-link' : 'nav-link'}
                >
                About
                </a>
            </li>
            <li className="nav-item">
                <a
                href="#projects"
                onClick={() => handlePageChange('Projects')}
                // Check to see if the currentPage is `Blog`, and if so we use the active link class from bootstrap. Otherwise, we set it to a normal nav-link
                className={currentPage === 'Projects' ? 'nav-link active active-link' : 'nav-link'}
                >
                Projects
                </a>
            </li>
            <li className="nav-item">
                <a
                href="#contact"
                onClick={() => handlePageChange('Contact')}
                // Check to see if the currentPage is `Contact`, and if so we use the active link class from bootstrap. Otherwise, we set it to a normal nav-link
                className={currentPage === 'Contact' ? 'nav-link active active-link' : 'nav-link'}
                >
                Contact
                </a>
            </li>
            <li className="nav-item">
                <a
                href="#resume"
                onClick={() => handlePageChange('Resume')}
                // Check to see if the currentPage is `Contact`, and if so we use the active link class from bootstrap. Otherwise, we set it to a normal nav-link
                className={currentPage === 'Resume' ? 'nav-link active active-link' : 'nav-link'}
                >
                Resume
                </a>
            </li>
        </ul>
    </header>
  );
}

export default Header;